/** @jsx React.DOM */
'use strict';

var React = require('react');
var TwitterTimeline = require('./TwitterTimeline');
var GitHubStuff = require('./GitHub/GitHubStuff');
var Header = require('./Header');

var App = React.createClass({displayName: 'App',
  render: function() {
    return (
      React.DOM.div(null, 
        Header(null), 
        React.DOM.div(null, 
          "Most of the interesting stuff about me happens in two places"
        ), 
        React.DOM.div({className: "row"}, 
          React.DOM.div({className: "col-sm-5 col-md-6"}, 
            React.DOM.h2(null, "GitHub"), 
            GitHubStuff(null)
          ), 
          React.DOM.div({className: "col-sm-7 col-md-6"}, 
            React.DOM.h2(null, "Twitter"), 
            TwitterTimeline(null)
          )
        )
      )
    );
  }
});

React.renderComponent(App(null), document.body);

module.exports = App;


