/** @jsx React.DOM */
'use strict';

var React = require('react');

var GitHubRepoCard = React.createClass({displayName: 'GitHubRepoCard',
  getDefaultProps: function () {
    return {
      repo: { owner: {} }
    };
  },
  render: function() {
    var repo = this.props.repo;
    if (!repo.name) {
      return React.DOM.div(null);
    }
    var shortHomepage = (repo.homepage || '').replace(/^.*?\:\/\//, '');
    console.log(shortHomepage);
    return (
      React.DOM.div({className: "github-card repo-card"}, 
        React.DOM.div({className: "header"}, 
          React.DOM.strong({className: "name"}, 
            React.DOM.a({href: repo.html_url}, repo.name), 
            React.DOM.sup({className: "language"}, repo.language)
          ), 
          React.DOM.span(null, 
            repo.fork ? 'Forked by' : 'Created by', 
            React.DOM.a({href: repo.owner.html_url}, repo.owner.login)
          ), 
          React.DOM.a({className: "button", href: repo.html_url}, "Star")
        ), 
        React.DOM.div({className: "content"}, 
          React.DOM.p(null, 
            repo.description, " ", React.DOM.a({href: repo.homepage}, shortHomepage)
          )
        )
      )
    );
  }
});

module.exports = GitHubRepoCard;


