/** @jsx React.DOM */
'use strict';

var React = require('react');
var GitHubRepoCard = require('./GitHubRepoCard');

var GitHubRepoList = React.createClass({displayName: 'GitHubRepoList',
  getDefaultProps: function() {
    return { repos: [] };
  },
  render: function() {
    var repos = this.props.repos.sort(function (a, b) {
      return Date.parse(b.pushed_at) - Date.parse(a.pushed_at);
    }).map(function(repo) {
      return GitHubRepoCard({key: repo.id, repo: repo});
    });
    return (
      React.DOM.div(null, 
        React.DOM.h3(null, "My ", repos.length, " Repositories"), 
        React.DOM.div(null, 
          repos
        )
      )
    );
  }
});

module.exports = GitHubRepoList;


