/** @jsx React.DOM */
'use strict';

var React = require('react');

var GitHubUserCard = React.createClass({displayName: 'GitHubUserCard',
  getDefaultProps: function () {
    return {
      user: {
        login: 'kentcdodds',
        avatar_url: 'https://avatars.githubusercontent.com/u/1500684?v=2',
        html_url: 'https://github.com/kentcdodds',
        name: 'Kent C. Dodds',
        company: 'AtTask, Inc.',
        public_repos: 63,
        public_gists: 48,
        followers: 29,
        created_at: '2012-03-04T22:32:01Z',
        updated_at: '2014-09-30T22:32:49Z'
      },
      orgCount: 7
    };
  },
  render: function() {
    var user = this.props.user;
    var orgCount = this.props.orgCount;
    if (!user) {
      return React.DOM.div(null);
    }
    return (
      React.DOM.div({className: "github-card user-card"}, 
        React.DOM.div({className: "header"}, 
          React.DOM.a({className: "avatar", href: user.html_url}, 
            React.DOM.img({src: user.avatar_url}), 
            React.DOM.strong(null, user.name), 
            React.DOM.span(null, "@", user.login, " | ", user.company)
          ), 
          React.DOM.a({className: "button", href: user.html_url}, "Follow")
        ), 
        React.DOM.ul({className: "status"}, 
          React.DOM.li(null, 
            React.DOM.a({href: 'https://github.com/' + user.login + '?tab=repositories'}, 
              React.DOM.strong(null, user.public_repos), "Repos")
          ), 
          React.DOM.li(null, 
            React.DOM.a({href: 'https://gist.github.com/' + user.login}, 
              React.DOM.strong(null, user.public_gists), "Gists")
          ), 
          React.DOM.li(null, 
            React.DOM.a({href: 'https://github.com/' + user.login + '/followers'}, 
              React.DOM.strong(null, user.followers), "Followers")
          ), 
          React.DOM.li(null, 
            React.DOM.a({href: user.html_url}, 
              React.DOM.strong(null, orgCount), "Orgs")
          )
        )
      )
    );
  }
});

module.exports = GitHubUserCard;


