/** @jsx React.DOM */
'use strict';

var React = require('react');

var Header = React.createClass({displayName: 'Header',
  getDefaultProps: function() {
    return {
      taglineItems: [
        { name: 'husband' },
        { name: 'father' },
        { name: 'AtTasker', url: 'http://www.attask.com' },
        { name: 'Egghead.io instructor', url: 'https://egghead.io/instructors/kentcdodds' },
        { name: 'web developer', url: 'http://www.github.com/kentcdodds' },
        { name: 'tweeter', url: 'http://twitter.com/kentcdodds' },
        { name: 'Mormon', url: 'http://www.mormon.org'  }
      ]
    };
  },
  render: function() {
    var taglineContents = this.props.taglineItems.map(function(item, index, arry) {
      var suffix = index < arry.length - 2 ? ', ' : '';
      if (index === arry.length - 2) {
        suffix = ', and ';
      }
      if (item.url) {
        return React.DOM.span({key: item.name}, React.DOM.a({href: item.url}, item.name), suffix);
      } else {
        return React.DOM.span({key: item.name}, item.name + suffix);
      }
    });

    return (
      React.DOM.header(null, 
        React.DOM.h1({className: "my-name"}, "Kent C. Dodds"), 
        React.DOM.div({className: "my-photo-container"}, 
          React.DOM.img({className: "my-photo", src: "/photo.png", alt: "Photo of Kent C. Dodds"}), 
          React.DOM.p({className: "my-photo-filler"}, 
            "Photo of me appears to not be loading... :-("
          )
        ), 
        React.DOM.p({className: "tagline"}, 
          "I am a ", taglineContents
        )
      )
    );
  }
});

module.exports = Header;


